"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useEffect, useState } from "react"
import Link from "next/link"

export default function AppDashboard() {
  const [pollutionRate, setPollutionRate] = useState(42)
  const [notifications, setNotifications] = useState([
    { id: 1, time: "2 min ago", message: "Pollution level normal at 42%", type: "success" },
    { id: 2, time: "15 min ago", message: "Engine RPM returned to normal", type: "info" },
    { id: 3, time: "1 hour ago", message: "Alert: Pollution reached 65%", type: "warning" },
  ])

  const carDetails = {
    carNumber: "MH-12-AB-1234",
    carModel: "Toyota Camry Hybrid 2024",
    ownerName: "Rajesh Kumar Sharma"
  }

  useEffect(() => {
    const interval = setInterval(() => {
      setPollutionRate(prev => {
        const change = (Math.random() - 0.5) * 3
        const newRate = Math.max(0, Math.min(100, prev + change))
        
        // Add notification for significant changes
        if (Math.abs(newRate - prev) > 2) {
          const newNotification = {
            id: Date.now(),
            time: "Just now",
            message: `Pollution level ${newRate > prev ? 'increased' : 'decreased'} to ${Math.round(newRate)}%`,
            type: newRate > 50 ? "warning" : "success"
          }
          
          setNotifications(prevNotifications => [
            newNotification,
            ...prevNotifications.slice(0, 4)
          ])
        }
        
        return newRate
      })
    }, 3000)

    return () => clearInterval(interval)
  }, [])

  const getNotificationIcon = (type: string) => {
    switch (type) {
      case "success": return "‚úÖ"
      case "warning": return "‚ö†Ô∏è"
      case "info": return "‚ÑπÔ∏è"
      default: return "üì±"
    }
  }

  const getNotificationColor = (type: string) => {
    switch (type) {
      case "success": return "border-green-500/30 bg-green-900/20"
      case "warning": return "border-yellow-500/30 bg-yellow-900/20"
      case "info": return "border-blue-500/30 bg-blue-900/20"
      default: return "border-gray-500/30 bg-gray-900/20"
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-gray-900 to-black text-white p-4">
      <div className="container mx-auto max-w-md">
        {/* Mobile Header */}
        <div className="flex justify-between items-center mb-6">
          <div>
            <h1 className="text-2xl font-bold bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent">
              EcoGuard Mobile
            </h1>
            <p className="text-gray-400 text-sm">Vehicle monitoring app</p>
          </div>
          <Link href="/">
            <Button variant="outline" size="sm" className="border-gray-600 text-gray-300 hover:bg-gray-800">
              ‚Üê Home
            </Button>
          </Link>
        </div>

        {/* Quick Status Card */}
        <Card className="mb-6 bg-black/40 border-blue-500/30 backdrop-blur-sm">
          <CardContent className="p-4">
            <div className="flex items-center justify-between">
              <div>
                <div className="text-sm text-gray-400">Current Status</div>
                <div className={`text-lg font-bold ${
                  pollutionRate < 50 ? 'text-green-400' : 'text-red-400'
                }`}>
                  {pollutionRate < 50 ? 'Clean Air' : 'Alert Mode'}
                </div>
              </div>
              <div className="text-right">
                <div className={`text-2xl font-bold ${
                  pollutionRate < 50 ? 'text-green-400' : 'text-red-400'
                }`}>
                  {Math.round(pollutionRate)}%
                </div>
                <div className="text-sm text-gray-400">Pollution</div>
              </div>
            </div>
            
            {/* Status Bar */}
            <div className="mt-4 bg-gray-700 rounded-full h-2">
              <div 
                className={`h-2 rounded-full transition-all duration-500 ${
                  pollutionRate < 50 ? 'bg-green-500' : 'bg-red-500'
                }`}
                style={{ width: `${pollutionRate}%` }}
              ></div>
            </div>
          </CardContent>
        </Card>

        {/* Vehicle Info */}
        <Card className="mb-6 bg-black/40 border-gray-600/30 backdrop-blur-sm">
          <CardHeader className="pb-3">
            <CardTitle className="text-lg text-white">Vehicle Details</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <div className="flex justify-between">
              <span className="text-gray-400">Number:</span>
              <span className="text-white font-mono">{carDetails.carNumber}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-400">Model:</span>
              <span className="text-white text-sm">{carDetails.carModel}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-400">Owner:</span>
              <span className="text-white">{carDetails.ownerName}</span>
            </div>
          </CardContent>
        </Card>

        {/* Quick Actions */}
        <div className="grid grid-cols-2 gap-4 mb-6">
          <Card className="bg-green-900/20 border-green-500/30 backdrop-blur-sm">
            <CardContent className="p-4 text-center">
              <div className="text-2xl mb-2">üå±</div>
              <div className="text-sm text-green-400 font-semibold">Eco Mode</div>
              <div className="text-xs text-gray-400">Active</div>
            </CardContent>
          </Card>
          
          <Card className="bg-blue-900/20 border-blue-500/30 backdrop-blur-sm">
            <CardContent className="p-4 text-center">
              <div className="text-2xl mb-2">‚öôÔ∏è</div>
              <div className="text-sm text-blue-400 font-semibold">Engine</div>
              <div className="text-xs text-gray-400">
                {pollutionRate > 75 ? 'Limited' : 'Normal'}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Notifications */}
        <Card className="bg-black/40 border-gray-600/30 backdrop-blur-sm">
          <CardHeader className="pb-3">
            <CardTitle className="text-lg text-white flex items-center gap-2">
              <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
              Recent Notifications
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            {notifications.map((notification) => (
              <div 
                key={notification.id}
                className={`p-3 rounded-lg border ${getNotificationColor(notification.type)}`}
              >
                <div className="flex items-start gap-3">
                  <span className="text-lg">{getNotificationIcon(notification.type)}</span>
                  <div className="flex-1">
                    <div className="text-sm text-white">{notification.message}</div>
                    <div className="text-xs text-gray-400 mt-1">{notification.time}</div>
                  </div>
                </div>
              </div>
            ))}
          </CardContent>
        </Card>

        {/* Pollution History Chart Placeholder */}
        <Card className="mt-6 bg-black/40 border-gray-600/30 backdrop-blur-sm">
          <CardHeader className="pb-3">
            <CardTitle className="text-lg text-white">Today's Pollution Trend</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="h-24 bg-gray-800/50 rounded-lg flex items-end justify-between p-2">
              {[30, 45, 38, 52, 48, 42, Math.round(pollutionRate)].map((value, index) => (
                <div key={index} className="flex flex-col items-center">
                  <div 
                    className={`w-4 rounded-t ${value < 50 ? 'bg-green-500' : 'bg-red-500'}`}
                    style={{ height: `${(value / 100) * 60}px` }}
                  ></div>
                  <div className="text-xs text-gray-400 mt-1">
                    {index === 6 ? 'Now' : `${6-index}h`}
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>

        {/* Emergency Button */}
        <div className="mt-6 text-center">
          <Button 
            className="w-full bg-red-600 hover:bg-red-700 text-white font-semibold py-3"
            size="lg"
          >
            üö® Emergency Support
          </Button>
          <p className="text-xs text-gray-400 mt-2">
            Contact support for immediate assistance
          </p>
        </div>
      </div>
    </div>
  )
}
